import React, { Component } from "react";
import "../profileprojects/profileprojects.css"


class AProfileProject extends Component {
    render() {
        return(
            <div>
                {this.props.compDisp == null ? <img alt={this.props.name} src={this.props.image} key={this.props.id} href={this.props.link}/> : null}
                {/* <img alt={this.props.name} src={this.props.image} key={this.props.id} href={this.props.link}/> */}
                {/* <a className="projA projBanner" href={this.props.link}>{this.props.alt}</a> */}
            </div>
        )
    }
}



export default AProfileProject