import React, { Component } from "react";
import "./profileprojects.css"
import "../individualProjects/individualProjects"
import IndividualProjects from "../individualProjects/individualProjects";
import AProfileProject from "../aprofileproject/aProfileProject"



class ProfileProject extends Component {
    constructor() {
        super();
        this.state = {
            compDisp: null
        }
    }

    

    mouseEnter = () => {
        // console.log("THIS IS MOUSE ENTER ON " + this.props.id + "!")
        // console.log(this.state.compDisp)
        this.setState({ compDisp: this.props.id });
        // console.log("state.compDisp is now set to " + this.state.compDisp)
    }

    mouseLeave = () => {
        // console.log("MOUSE LEFT, AMAGGEDON " + this.props.id + "!")
        // console.log(this.state.compDisp)
        this.setState({ compDisp: null })
        // console.log(this.state.compDisp)
    }


    render() {
        return (
            <div className="portfolioBox" onMouseEnter={this.mouseEnter} onMouseLeave={this.mouseLeave}>
                <AProfileProject 
                    key={this.props.id}
                    id={this.props.id}
                    alt={this.props.name}
                    image={this.props.image}
                    link={this.props.link}
                    title={this.props.title}
                    tech={this.props.tech}
                    info={this.props.info} 
                    onMouseEnter={this.mouseEnter}
                    onMouseLeave={this.mouseLeave}
                    compDisp={this.state.compDisp} />
                {/* {this.state.compDisp === null ? <IndividualProjects /> : null} */}
                {this.state.compDisp === null ? <a className="projA projBanner" href={this.props.link}>{this.props.alt}</a> : null}
                {this.state.compDisp !== null ? <IndividualProjects title={this.props.title} tech={this.props.tech} info={this.props.info} link={this.props.link} /> : null}

            </div>


        );
    }
}

export default ProfileProject;
